<template>

  <div>
	风险分类台账:<input type="file" @change="handleFileUpload(1)" accept=".xls,.xlsx" name=""/>

    在库押品信息:<input type="file" @change="handleFileUpload(2)" accept=".xls,.xlsx" name="在库押品信息"/>
	
	<button @click="donload()">下载</button>
    <pre>{{ formattedOutput }}</pre>
	
  </div>

</template>


<script>

export default {

  data() {

    return {

      table1Data: [], // 假设这是从第一个Excel文件获取的数据

      table2Data: [], // 假设这是从第二个Excel文件获取的数据

      formattedOutput: [], // 重组后的输出数据
	  
	  formattedDate: this.formatDate(new Date()),

    };

  },

  methods: {
	  formatDate(date) {
	  
	        // 获取年月日
	  
	        const year = date.getFullYear().toString();
	  
	        const month = (date.getMonth() + 1).toString().padStart(2, '0');
	  
	        const day = date.getDate().toString().padStart(2, '0');
	  
	        
	  
	        // 返回格式化的日期
	  
	        return `${year}${month}${day}`;
	  
	      },
	  
	  donload() {
		const worksheet = this.$xlsx.utils.json_to_sheet(this.formattedOutput);
		
		const workbook = this.$xlsx.utils.book_new();
		
		this.$xlsx.utils.book_append_sheet(workbook, worksheet, "Sheet1");
		
		
		// 将工作簿写入二进制文件
		
		const excelBuffer = this.$xlsx.write(workbook, { bookType: 'xlsx', type: 'buffer' });
		
		
		// 将二进制文件转换为Blob，以便下载
		
		const blob = new Blob([excelBuffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
		
		
		// 创建隐藏的可下载链接
		
		const url = window.URL.createObjectURL(blob);
		
		const a = document.createElement('a');
		
		a.href = url;
		
		a.download = '灵西支行在库押品'+this.formattedDate+'.xlsx'; // 设置下载文件名
		
		document.body.appendChild(a);
		
		a.click(); // 触发点击
		
		window.URL.revokeObjectURL(url); // 清理
		
		document.body.removeChild(a);  
	  },
handleFileUpload(tableIndex) {

      const file = event.target.files[0];

      if (!file) return;


      const reader = new FileReader();

      reader.onload = (e) => {

        const binaryStr = e.target.result;

        const workbook = this.$xlsx.read(binaryStr, { type: 'binary' });


        const sheetName = workbook.SheetNames[0];

        const worksheet = workbook.Sheets[sheetName];


        const data = this.$xlsx.utils.sheet_to_json(worksheet, { header: 1 });
		// const data = this.$xlsx.utils.sheet_to_json(worksheet, {raw:true});
		
        if (tableIndex === 1) {

          this.table1Data = data; // 第四列及第一列

        } else if (tableIndex === 2) {

          this.table2Data = data; // 第七列及第一列
		this.getTable2Value(this.table1Data,data);
          this.reformatData();

        }
	
      };

      reader.readAsBinaryString(file);

    },

    reformatData() {

      if (!this.table1Data.length || !this.table2Data.length) {

        console.error('Both tables must have data before formatting.');

        return;

      }


      // 假设第一个表格的数据结构与所需格式相符

      this.formattedOutput = this.table1Data.map(row => ({

        经办机构: '灵西支行', // 固定值

        借款人客户号: row[2], // 第一个表的第三列

        借款人名称: row[3], // 第一个表的第四列

        合同编号: row[1], // 第一个表的第二列

        '合同金额(元)': row[15], // 第一个表的第十六列

        '贷款余额(元)': row[7], // 第一个表的第八列

        贷款五级分类2: (row[9]+'').substring(0, (row[9]+'').length - 1)+'类', // 空白

        贷款五级分类: row[9], // 第一个表的第十列

        担保方式: row[6], // 第一个表的第七列

        抵质押物种类: '', // 空白

        押品类型: this.getTable2Value(row, 2), // 第二个表的第三列，需根据匹配逻辑调整

        '押品类型(小类)': this.getTable2Value(row, 4), // 第二个表的第五列

        抵质押人客户号: row[2], // 与第二列相同

        抵质押人名称: row[3], // 与第三列相同

        权利证书类型: this.getTable2Value(row, 7), // 第二个表的第八列

        '评估金额（元）': this.getTable2Value(row, 10), // 第二个表的第十一列

        '设定权利金额（元）': this.getTable2Value(row, 11), // 第二个表的第十二列

        经办人: this.getTable2Value1(row, 15), // 第二个表的第十六列
		
		备注: '',
      }));
	  console.log(this.formattedOutput);

    },

    // 假设存在某种逻辑来匹配第一个表格的行与第二个表格的行，这里简化处理

	getTable2Value1(rowFromTable1, columnIndex){
		
		
		  // 实际应用中，您可能需要根据具体的匹配逻辑来获取第二个表格的数据
		
		  // 例如，如果两个表格有共同的键值（如ID）用于匹配，那么这里应该实现相应的查找逻辑
		
		  // 这里仅作为示例，返回空字符串  1--16hang  	2 --9
		
		    let matchingRow = this.table2Data.find(row => ((row[6] === rowFromTable1[3])&&(row[11] === rowFromTable1[15])));
			console.log('直接一次好');
			if(matchingRow === undefined) {
				matchingRow = this.table2Data.find(row => row[6] === rowFromTable1[3]);
				if(matchingRow !== undefined) {
					console.log('重新获取到');
				}else{
					console.log('第二次未获取到');
				}
			}else{
				matchingRow[6] += '1';
				console.log('修改值');
			}
		  console.log(matchingRow);
		    // 如果找到了匹配项，则返回指定列的值；否则，返回默认值（例如空字符串）
		  
		    return matchingRow ? matchingRow[columnIndex] : '';
		
		  // 实现真正的查找逻辑，例如：
		
		  // return this.table2Data.find(row => row.id === rowFromTable1.id)[columnIndex];
		
		
	},
    getTable2Value(rowFromTable1, columnIndex) {

      // 实际应用中，您可能需要根据具体的匹配逻辑来获取第二个表格的数据

      // 例如，如果两个表格有共同的键值（如ID）用于匹配，那么这里应该实现相应的查找逻辑

      // 这里仅作为示例，返回空字符串  1--16hang  	2 --9

        let matchingRow = this.table2Data.find(row => ((row[6] === rowFromTable1[3])&&(row[11] === rowFromTable1[15])));
		console.log('直接一次好');
		if(matchingRow === undefined) {
			matchingRow = this.table2Data.find(row => row[6] === rowFromTable1[3]);
			if(matchingRow !== undefined) {
				console.log('重新获取到');
			}else{
				console.log('第二次未获取到');
			}
		}
      console.log(matchingRow);
        // 如果找到了匹配项，则返回指定列的值；否则，返回默认值（例如空字符串）
      
        return matchingRow ? matchingRow[columnIndex] : '';

      // 实现真正的查找逻辑，例如：

      // return this.table2Data.find(row => row.id === rowFromTable1.id)[columnIndex];

    },

  },

  mounted() {

    // 假设在mounted生命周期钩子或其他适当位置，数据已经被正确填充

    this.reformatData();

  },

};

</script>